import Helper from '@/function.js';
export default {
	data() {
		return {
			token: "",
			balance: 0,
			bankInfo: {},
			money: ""
		}


	},
	onLoad: function() {
		this.token = uni.getStorageSync('token');
		uni.removeStorageSync("bank");
		this.bankInfo={};
		this.getData();
	},
	onShow: function() {
		var item = uni.getStorageSync("bank");
		if (!Helper.isEmpty(item)) {
			this.bankInfo = item;
		}
	},
	methods: {
		checkInput(e) {

			if (parseFloat(this.money) > parseFloat(this.balance)) {
				this.money = this.balance + ""
				console.log(this.money, this.balance)
			} else {

			}
		},
		setMoney() {
			this.money = this.balance;
		},
		getData() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTodayData',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.balance = res.data.data.money;
					} else {

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		goSendData() {
			//
			if (!Helper.validMoney(this.money)) {
				uni.showModal({
					title: "出错了",
					content: "请输入正确的金额",
					showCancel: false,
				})
				return;
			}

			if (Helper.isEmpty(this.bankInfo.bankName)) {
				uni.showModal({
					title: "出错了",
					content: "请选择提现银行卡",
					showCancel: false,
				})
				return;
			}

			if (parseFloat(this.money) > parseFloat(this.balance)) {
				this.money = this.balance
			}

			var _this = this;

			var params = {
				"money": this.money,
				"bankName": this.bankInfo.bankName,
				"subBankName": this.bankInfo.subBankName,
				"cardNo": this.bankInfo.cardNo,
				"accountName": this.bankInfo.accountName,
			}

			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberAddMoneyOrders',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						uni.showModal({
							content: res.data.errorMessage,
							showCancel: false,
							success:function(res){
								uni.reLaunch({
									url: '/pages/my/result?bankName=' + _this.bankInfo.bankName + "&money=" + _this.money
								});
							}
						})
						
					} else {
						uni.showModal({
							title: "出错了",
							content: res.data.errorMessage,
							showCancel: false,
						})
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})



		},
		chooseback() {
			uni.removeStorageSync("bank")
			this.bankInfo={};
			uni.navigateTo({
				url: '/pages/my/chooseback'
			});
		},
		bank() {
			uni.navigateBack({
				delta: 1
			})


		}


	}


}
